import { DeepPartial } from '../core'
import { Wallet, WalletReferred, WalletSignature } from './wallet.model'

/**
 * Wallet Mock
 */
export function WalletMock(options?: DeepPartial<Wallet>): Wallet {
  const now = new Date().toISOString()

  return {
    _id: options?._id || 'WALLET_ID',

    userId: options?.userId,
    address: options?.address || 'WALLET_ADDRESS',
    signatures: options?.signatures?.map((s) => WalletSignatureMock(s)),

    createdAt: now,
    createdBy: options?.createdBy || 'USER_ID',
    updatedAt: now,
    updatedBy: options?.updatedBy || 'USER_ID',
  }
}

export function WalletSignatureMock(
  options?: Partial<WalletSignature>
): WalletSignature {
  const now = new Date().toISOString()
  return {
    _id: options?._id || 'SIGNATURE_ID',
    hash: options?.hash || 'SIGNATURE_HASH',
    signedOn: options?.signedOn || now,
  }
}

export function WalletReferredMock(
  options?: Partial<WalletReferred>
): WalletReferred {
  const now = new Date().toISOString()
  return {
    _id: options?._id || 'WALLET_ID',
    address: options?.address || 'WALLET_ADDRESS',
    referralCode: options?.referralCode || 'WALLET_REFERRAL_CODE',
    activatedOn: options?.activatedOn || now,
  }
}
